@tailwind base;
@tailwind components;
@tailwind utilities;

body {
    overflow-x: hidden;
}

* {
    box-sizing: border-box;
}

::-webkit-scrollbar {
    width: 10px;
}
  
::-webkit-scrollbar-thumb {
    background-color: rgba(62, 64, 71, 0.5);
    border-radius: 6px;
}
  
::-webkit-scrollbar-track {
    background-color: #18181a;
}

.animated-first {
    animation: start-animation linear forwards 0.6s;
}

.animated-second {
    animation: second-animation linear forwards 2.4s;
}

.custom-background {
    background-image: url("assets/Banner.jpg");

    filter: blur(4px);
    -webkit-filter: blur(4px);

    animation: start-background 0.5s linear forwards;
}

.background-color {
    background: rgba(32, 32, 36, 0);
}

.custom-font {
    font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif
}

@keyframes start-background {
    0% {opacity: 0; height: 95%;}
    100% {opacity: 1; height: 100%;}
}

@keyframes start-animation {
    0% {opacity: 0; transform: translateY(1%);}
    100% {opacity: 1; transform: translateY(-1%);}
}

@keyframes second-animation {
    0% {opacity: 0; transform: translateY(2%);}
    80% {opacity: 0;}
    100% {opacity: 1; transform: translateY(-2%);}
}